@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&family=Cedarville+Cursive&family=DynaPuff:wght@400;500;600;700&family=Oswald:wght@500&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* for the search section */
.search{
    background-color: black;
    height: 420px;
    color: #e50914;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* .search img{
    width: 40%;
} */
.search form{
    width: 50%;
    display: flex;
    justify-content: center;
}
.search input{
    width: 85%;
    padding: 14px;
    border: 0;
    border-radius: 20px;
}
.search input:focus{
    outline: 0;
}




.movie{
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 16px;
}
.heading{
    color: #e50914;
    font-size: 60px;
}
.movie-group{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    padding: 20px 5%;
    gap: 24px;
    justify-content: center;
    align-content: center;
}


.movie-item{
    background-color: white;
}
.movie-item img{
    width: 100%;
}